import React, { useState } from 'react';
import Certification from '../Certification/Certification';
import Skills from '../Skills/Skills';
import './About.css';

const About = () => {
  const [val, setVal] = useState({
    tab1: true,
    tab2: false,
  });

  const { tab1, tab2 } = val;

  // useEffect(() => {
  //   window.onresize = () => {
  //     checkWindowResize();
  //   };
  // });

  const handleClick = tab => e => {
    console.log(e.target.className);
    if (tab === 'tab2') {
      setVal({ ...val, tab1: false, tab2: true });
    }
    if (tab === 'tab1') {
      setVal({ ...val, tab1: true, tab2: false });
    }
  };

  return (
    <>
      <div className="tab-container">
        {/* <button
          className={tab1 ? 'tablink active' : 'tablink'}
          onClick={handleClick('tab1')}
        >
          Who am I
        </button> */}
        <button
          className={tab1 ? 'tablink active' : 'tablink'}
          onClick={handleClick('tab1')}
        >
          Skills
        </button>
        <button
          className={tab2 ? 'tablink active' : 'tablink'}
          onClick={handleClick('tab2')}
        >
          Certifications
        </button>
      </div>
      {/* {tab1 && WhoAmI()} */}
      {tab1 && <Skills />}
      {tab2 && <Certification />}
    </>
  );
};

export default About;
